<template>
  <div class="card">
    <div class="wrapper">
      <h2>GS#{{ gearSet.GS }}</h2>

      <div
      v-if="gearSet.color == 'Black'"
        class="info-container"
        :style="whiteTextObject"
      >      
        <h3>{{ gearSet.color }}</h3>
        <h3>{{ gearSet.model }}</h3>        
      </div>

      <div
        class="info-container"
        :style="colorObject"
        v-else
      >      
        <h3>{{ gearSet.color }}</h3>
        <h3>{{ gearSet.model }}</h3>
      </div>


      <div class="pinion-container">
        <div class="image-container">
          <img src="../assets/pinion.jpg" alt="" />
        </div>
        <div class="data-container">
          <h5>Serial Number:</h5>
          <h4>{{ gearSet.pinionNumber }}</h4>
        </div>
      </div>
      <div class="ring-container">
        <div class="image-container">
          <img src="../assets/ring.jpg" alt="" />
        </div>
        <div class="data-container">
          <h5>Serial Number:</h5>
          <h4>{{ gearSet.ringNumber }}</h4>
        </div>
      </div>
    </div>
  </div>
</template> 

<script>
import { reactive } from "vue";
export default {
  props: ["gearSet"],

  setup(props) {
    const colorObject = reactive({
      backgroundColor: props.gearSet.color,    
    });
    const whiteTextObject = reactive({
      backgroundColor: props.gearSet.color,    
      color: "white"
    })
    
    return {
      colorObject,
      whiteTextObject
    };
  },
};
</script>

<style scoped>
.wrapper h2 {
  text-align: center;
  margin-bottom: 15px;
}
.card {
  padding: 15px;
  border-radius: 10px;
  transition: 0.2s;
  margin: 10px;
  border: 1px solid #666;
}
.card:hover {
  transform: scale(1.03);
  box-shadow: 2px 4px 8px rgba(0, 0, 0, 0.45);
}
.info-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px;
  padding: 5px;
  border-radius: 5px;
}
.pinion-container,
.ring-container {
  display: flex;
  margin-bottom: 10px;
}
.data-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-left: 20px;
}
.data-container h5 {
  color: #666;
}
.data-container h4 {
  font-size: 20px;
  font-weight: 900;
}

img {
  width: 100%;
  height: 100px;
  object-fit: cover;
  border-radius: 10px;
}

</style>